.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

main{
  height: 100vh;
}

.App-header,main {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(15px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

#install-popup img{
  max-width: calc(30px + 2vmin);
  max-height: calc(30px + 2vmin);
}

/* @media screen and (max-width:540px){ */
  #install-popup { 
    position: absolute;
    bottom: 0;
    width: 100vw;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: row;
    padding: calc(5px + 2vmin);
    color: blue;
    font-weight: 500;
    background-color: white;
  }
/* } */

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
